.loader, .loader:before, .loader:after {
  width: 1em;
  height: 4em;
  background: #fff;
  animation: load1 1s ease-in-out infinite;
}

.loader {
  color: #fff;
  text-indent: -9999em;
  margin: 88px auto;
  font-size: 11px;
  animation-delay: -.16s;
  position: relative;
  transform: translateZ(0);
}

.loader:before, .loader:after {
  content: "";
  position: absolute;
  top: 0;
}

.loader:before {
  animation-delay: -.32s;
  left: -1.5em;
}

.loader:after {
  left: 1.5em;
}

@keyframes load1 {
  0%, 80%, 100% {
    height: 4em;
    box-shadow: 0 0;
  }

  40% {
    height: 5em;
    box-shadow: 0 -2em;
  }
}

